$colors: #662d8c, #2e3192, #ed1c24;
$images: '1.jpg', '2.jpg', '3.jpg';

body {
  height: 100vh;
  overflow: hidden;
}

input {
  display: none;
}

@for $i from 1 through length($colors) {
  input#section#{$i}:checked ~ nav label[for='section#{$i}'] {
    background-color: #fff;
  }

  input#section#{$i}:checked ~ section:nth-of-type(#{$i}) {
    bottom: 0;
    transition: .5s ease;
    z-index: 1;
  }

  input#section#{$i}:checked ~ .cover {
    background-color: nth($colors, $i);
  }
}

nav {
  position: absolute;
  right: 2vw;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;

  label {
    display: block;
    width: 14px;
    height: 14px;
    margin: 12px 0;
    border: 2px solid #fff;
    border-radius: 50%;
    cursor: pointer;
    transition: .3s ease;

    &:hover {
      background-color: #fff;
    }
  }
}

section {
  position: absolute;
  bottom: 100%;
  right: 0;
  left: 0;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition-delay: .5s;

  @for $i from 1 through 3 {
    &:nth-of-type(#{$i}) {
      background-color: nth($colors, $i);
    }
  }
}